export default {
	data() {
		return {
			tip: '',
			type: 'password',
			isuser: false,
			isemail: false,
			look: false,
			invalid: false,
			emailEmploy: false,
			userEmploy: false,
			signupForm: {
				user: '',
				email: '',
				pw: ''
			},
			ok: false
		};
	},
	methods: {
		// 注册
		async signup() {
			if (!this.ok) {
				return this.tip = '请将信息填写完整';
			}
			console.log(this.signupForm)
			const res = await this.$myRequest({
				url: '/signup',
				method: 'post',
				data: this.signupForm
			})
			console.log((res))
		},
		//返回上一页
		navBack() {
			uni.navigateBack({})
		},
		//密码显示与隐藏
		changeType() {
			this.type = this.type == 'password' ? 'text' : 'password';
			this.look = !this.look;
		},
		//邮箱验证
		emailVaild(e) {
			const email = e.detail.value
			// 没有填写信息不验证
			if (email.length == 0) {
				return;
			}
			const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			if(reg.test(email)){
				this.signupForm.email = email;
				this.isemail = true;
				this.invalid = false;
			}else{
				this.signupForm.email = null;
				this.isemail = false;
				this.invalid = true;
			}
			this.isOK()
		},
		userVaild(e) {
			this.signupForm.user = e.detail.value
			this.isuser = true
			
			console.log(e.detail.value)
			this.isOK()
		},
		//密码验证
		pwVaild(e) {
			this.signupForm.pw = e.detail.value;
			this.isOK()
		},
		//判断三个信息是否都可用
		isOK() {
			if (this.isuser && this.isemail && this.signupForm.pw.length > 5) {
				this.ok = true
			} else {
				this.ok = false
			}
		}
	}
}
